import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { DataService } from '../../services/data.service';
import { Router, Event, NavigationEnd } from '@angular/router';
declare var $;

@Component({
  selector: 'app-edit-profile',
  templateUrl: './edit-profile.component.html',
  styleUrls: ['./edit-profile.component.css']
})
export class EditProfileComponent implements OnInit {
  editProfile: FormGroup;
  profilePicture: any;
  userName: any;
  profilePic: any;
  otp: any;
  currUrl: string;

  constructor(public dataService: DataService, public route: Router) {
    this.editProfile = new FormGroup({
      firstName: new FormControl('', Validators.compose([
        Validators.required
      ])),
      lastName: new FormControl('', Validators.compose([
        Validators.required
      ])),
      email: new FormControl('', Validators.compose([
        Validators.required,
        Validators.pattern(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,3}))$/)
      ]))
    });

    this.route.events.subscribe((event: Event) => {
      if (event instanceof NavigationEnd) {
        this.currUrl = event.url.split('/')[1]
      }
    })
   }

  ngOnInit() {
    this.getProfile();
  }

  getProfile() {
    if (navigator.onLine){
      this.dataService.spinnerShow();
    this.dataService.post('admin/viewAdmin',{adminId: JSON.parse(localStorage.getItem('remittance')).id}, 1).subscribe(
      (response: any) => {
        this.dataService.spinnerHide();
        if (response['response_code'] == 200) {
          this.editProfile.setValue({
            firstName: (response.result.firstName)?(response.result.firstName):'',
            lastName: (response.result.lastName)?(response.result.lastName):'',
            email: response.result.email
          })
          this.userName = response.result.userName;
          this.profilePic = response.result.profilePic;
        }
        else if (response.response_code == 403 || response.response_code == 409 || response.response_code == 401 || response.response_code == 404) {
          this.dataService.errorToastr(response.response_message)
          this.dataService.logOut();
        } else {
          this.dataService.errorToastr(response[`response_message`])
        }
      },
      err => {
        this.dataService.spinnerHide();
        this.dataService.errorToastr('Something went wrong')
      })
    } else
    this.dataService.errorToastr('Your internet connection seems to be lost')
  }

    //----------------------------------------- CHANGE IMAGE FUNCTIONALITY STARTS -----------------------------------------//
    changeImage(event) {
      if(event.target.files && event.target.files[0]) {
        let img = event.target.files[0].type.split("/")
        if(img[1] == 'PNG'|| img[1] == 'png' || img[1]=='jpg' || img[1]=='JPG' || img[1]=='JPEG' || img[1]=='jpeg'){
          var reader = new FileReader()
          reader.onload = (event: any) => {
            this.profilePic = event.target.result;
          }
          reader.readAsDataURL(event.target.files[0])
        }
        else
          this.dataService.errorToastr('Please select a valid image file. Images with type png, jpg and jpeg are allowed only.')
      }
    }
    //----------------------------------------- CHANGE IMAGE FUNCTIONALITY ENDS -----------------------------------------//

    verify() {
      if (navigator.onLine){
        this.dataService.spinnerShow();
        let data = {
          "adminId": JSON.parse(localStorage.getItem('remittance')).id,
          "otp": this.otp,
          "email": this.editProfile.value.email
        }
      this.dataService.post('admin/verifyOtpAdmin',{adminId: JSON.parse(localStorage.getItem('remittance')).id}, 1).subscribe(
        (response: any) => {
          this.dataService.spinnerHide();
          if (response['response_code'] == 200) {
            $('#otp').modal('hide')
            this.dataService.successToastr(response.response_message)
          }
          else if (response.response_code == 403 || response.response_code == 409 || response.response_code == 401) {
            this.dataService.errorToastr(response.response_message)
            this.dataService.logOut();
          } else {
            this.dataService.errorToastr(response[`response_message`])
          }
        },
        err => {
          this.dataService.spinnerHide();
          this.dataService.errorToastr('Something went wrong')
        })
      } else
      this.dataService.errorToastr('Your internet connection seems to be lost')
    }

  updateProfile() {
    if (navigator.onLine){
      this.dataService.spinnerShow();
      let data = {
        "adminId": JSON.parse(localStorage.getItem('remittance')).id,
        "lastName": this.editProfile.value.lastName,
        "firstName": this.editProfile.value.firstName
      }
    this.dataService.post('admin/updateAdmin',{adminId: JSON.parse(localStorage.getItem('remittance')).id}, 1).subscribe(
      (response: any) => {
        this.dataService.spinnerHide();
        if (response['response_code'] == 200) {
          this.getProfile();
        }
        else if (response['response_code'] == 201) {
          $('#otp').modal('show');
        }
        else if (response.response_code == 403 || response.response_code == 409 || response.response_code == 401) {
          this.dataService.errorToastr(response.response_message)
          this.dataService.logOut();
        } else {
          this.dataService.errorToastr(response[`response_message`])
        }
      },
      err => {
        this.dataService.spinnerHide();
        this.dataService.errorToastr('Something went wrong')
      })
    } else
    this.dataService.errorToastr('Your internet connection seems to be lost')
  }

}



