import { Router, Event, NavigationEnd } from '@angular/router';
import { DataService } from './../../services/data.service';
import { Component, OnInit } from '@angular/core';
import * as ClassicEditor from '@ckeditor/ckeditor5-build-classic';

@Component({
  selector: 'app-terms-conditions-editor',
  templateUrl: './terms-conditions-editor.component.html',
  styleUrls: ['./terms-conditions-editor.component.css']
})
export class TermsConditionsEditorComponent implements OnInit {

  public Editor = ClassicEditor;
  model: any = {
    editorData: '<p>What is Lorem Ipsum?</p>'
};
  currUrl: string;
  constructor(public dataService: DataService, public router : Router) {
    this.router.events.subscribe((event: Event) => {
      if (event instanceof NavigationEnd) {
        this.currUrl = event.url.split('/')[1]
      }
    })
   }

  ngOnInit() {
    let headers = {
      "Authorization": "Basic "+btoa("walletapp:SFDSA78FSA7FFDSAFJRE32DSF98DSGFDGFDSH9H8FDSGFR4R549898DFDS"),
    }
// console.log("headers", headers)
    this.dataService.get("staticContent/getStaticContent/term_condition", headers).subscribe(
      (res: any)=>{
        if(res.response_code == 200){
          this.model.editorData = res.result.description;
        }
      },
      (err: any)=>{
        console.log("error", err);
      }
    );
  }

  submit(){
    console.log("userid",(JSON.parse(localStorage.getItem("remittance"))).id);
    this.dataService.post("staticContent/updateStaticContent",{
      "staticId": (JSON.parse(localStorage.getItem("staticId"))),
      "type":"term_condition",
      "title":"Terms&Conditions",
      "description": this.model.editorData
    }, '').subscribe(
      (res: any)=>{
        console.log("Response", res);
        if(res.response_code == 200){
          this.dataService.toaster.dismissAllToastr();
          this.dataService.successToastr(res.response_message);
          this.router.navigate(['/terms-conditions'])
        }else{
          this.dataService.toaster.dismissAllToastr();
          this.dataService.errorToastr(res.response_message);
        }
      },
      (err: any)=>{
        console.log("Error", err);
        this.dataService.toaster.dismissAllToastr();
        this.dataService.errorToastr("Please try again later!!")
      }
    )
  }

}



