import { ToastrManager } from 'ng6-toastr-notifications';
import { DataService } from './../../services/data.service';
import { Component, OnInit } from '@angular/core';
import { Router, Event, NavigationEnd } from '@angular/router';
// import { element } from 'protractor';

declare var CanvasJS: any;

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {
  noOfUsers: any;
  totalTransaction: any;
  currUrl: string;
  transactionData: any = 'Yearly';
  monthChartData: any = '';
  graphData: any = [];
  graphDisplay: any = []
  totalVander: any;
  transactionData1: any = 'Yearly';
  graphDisplay1: any[];
  constructor(public dataService: DataService, public toaster: ToastrManager, public route: Router) {
    this.route.events.subscribe((event: Event) => {
      if (event instanceof NavigationEnd) {
        this.currUrl = event.url.split('/')[1]
      }
    })
  }

  ngOnInit() {
    this.pageChange(1)
    this.onChange(1)
    this.onChange1(1);
    this.canvasChart()
    this.canvasChartAmount();
    if (navigator.onLine) {
      this.dataService.spinnerShow();
      this.dataService.get("admin/totalUser", '').subscribe(
        (res: any) => {
          this.dataService.spinnerHide();
          if (res.response_code == 200) {
            this.noOfUsers = res.result.result;
          }
          if (res.response_code == 403 || res.response_code == 409 || res.response_code == 404 || res.response_code == 401) {
            this.toaster.errorToastr(res.response_message);
            this.dataService.logOut();
          }
        },
        (err: any) => {
         this.dataService.spinnerHide();
          console.log("Error", err);
        }
      );
      this.dataService.spinnerShow();
      this.dataService.get("admin/totalTransaction", 1).subscribe(
        (res) => {
          this.dataService.spinnerHide();
          this.totalTransaction = res.result.result;
        },
        (err: any) => {
          this.dataService.spinnerHide();
          console.log("Error", err);
        }
      );
    } else {
      this.toaster.dismissAllToastr();
      this.toaster.errorToastr("Your internet connection seems to be lost!");
    }
  }
  canvasChart() {
    var chart = new CanvasJS.Chart("chartContainer");
    // chart.options.axisY = { prefix: "$", suffix: "K", includeZero: false };
    chart.options.axisY = { includeZero: false };
    chart.options.title = { text: "Total Transactions" };
    var series1 = { //dataSeries - first quarter
      type: "column",
      name: "First Quarter",
      indexLabelFontColor: "#5A5757",
      indexLabelPlacement: "outside",
      showInLegend: true,
      dataPoints: [] = this.graphDisplay,
    };
    chart.options.data = [];
    chart.options.data.push(series1);
    chart.render();
  }

  onChange(event) {
    this.graphDisplay = []
    this.dataService.spinnerShow();
    this.dataService.post('admin/userGraph', { "dayCount": this.transactionData }, 1).subscribe((data: any) => {
      this.dataService.spinnerHide();
      this.graphData = data.totalTransaction;
      this.graphData.forEach(element => {
        this.graphDisplay.push({ label: element.day.toString(), y: element.count, indexLabel: element.day.toString() });
      })
      this.canvasChart();
    },error=>{
      this.dataService.spinnerHide();
    })

  }
  pageChange(currentPage) {
    this.dataService.spinnerShow();
    this.dataService.post("admin/searchVendors", {
      page: currentPage
    }, 1).subscribe(
      (res: any) => {
        this.dataService.spinnerHide();
        if (res.response_code == 200) {
          this.totalVander = res.result.total
        } else if (res.response_code == 403 || res.response_code == 409 || res.response_code == 404 || res.response_code == 401) {
          this.dataService.errorToastr(res.response_message);
          // this.dataService.logOut();
        } else {
          this.dataService.errorToastr("Cannot get the users.");
        }
      },
      (err: any) => {
        this.dataService.spinnerHide();
        this.dataService.errorToastr("Cannot get the users.")
      }
    );
  }
  canvasChartAmount() {
    var chart = new CanvasJS.Chart("chartContainer1");
    chart.options.axisY = { prefix: "$", includeZero: false };
    chart.options.title = { text: "Total volume of transaction" };
    var series1 = { //dataSeries - first quarter
      type: "column",
      name: "First Quarter",
      indexLabelFontColor: "#5A5757",
      indexLabelPlacement: "outside",
      showInLegend: true,
      dataPoints: [] = this.graphDisplay1,
    };
    chart.options.data = [];
    chart.options.data.push(series1);
    chart.render();
  }
  onChange1(event) {
    this.graphDisplay1 = []
    this.dataService.spinnerShow();
    this.dataService.post('admin/userGraphVolume', { "dayCount": this.transactionData1 }, 1).subscribe((data: any) => {
      this.dataService.spinnerHide();
      this.graphData = data.totalTransaction;
      this.graphData.forEach(element => {
        this.graphDisplay1.push({ label: element.day.toString(), y: element.count, indexLabel: element.day.toString() });
        console.log('ststus ', this.graphDisplay1)
      })
      this.canvasChartAmount();
    },error=>{
      this.dataService.spinnerHide();
    })

  }
}



