import { Component, OnInit } from '@angular/core';
import { Router } from "@angular/router";
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { DataService } from '../../services/data.service';
declare var $: any;

@Component({
  selector: 'app-forgot-password',
  templateUrl: './forgot-password.component.html',
  styleUrls: ['./forgot-password.component.css']
})
export class ForgotPasswordComponent implements OnInit {

  forgotPasswordForm: FormGroup;
  otp: any;
  arrayg = [1,2,3,4,5,6];
  userid: any;
  constructor(private route: Router, public dataService: DataService) { }

  ngOnInit() {
    this.forgotPasswordForm = new FormGroup({
      email: new FormControl('', Validators.compose(
        [
          Validators.required,
            Validators.pattern(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,3}))$/)
        ]
      ))
    })
  }

  submit() {
    this.dataService.spinnerShow();
    let data = {
      "email":this.forgotPasswordForm.value.email
    }
    this.dataService.post("admin/forgotPasswordAdmin", data, 0)
    .subscribe(
      (res: any)=>{
        this.dataService.spinnerHide();
        console.log("response",res);
        if(res.response_code == 201 || res.response_code == 200){
          this.otp = ''
          $('#otpModal').modal('show');
          this.forgotPasswordForm.reset();
          this.userid = res.result[0];
        }
        else
          this.dataService.errorToastr(res.response_message)
      },
      (err: any)=>{
        this.dataService.spinnerHide();
        this.dataService.errorToastr("Please try again later.")
      })
  }

  verifyOTP() {
    this.dataService.spinnerShow();
    let data = {
      "userId":this.userid,
      "otp":this.otp
    }
    this.dataService.post("admin/verifyOtp", data, 0)
    .subscribe(
      (res: any)=>{
        this.dataService.spinnerHide();
        if(res.response_code == 200){
          this.dataService.successToastr(res.response_message);
          $('#otpModal').modal('hide');
          this.route.navigate(['/reset-password/'+this.userid])
        }
        else
          this.dataService.errorToastr(res.response_message)
      },
      (err: any)=>{
        this.dataService.spinnerHide();
        this.dataService.errorToastr("Please try again later.")
      })
  }
}



