import { Component, OnInit } from '@angular/core';
import { Router, Event, NavigationEnd } from '@angular/router';
import { DataService } from '../services/data.service';


@Component({
  selector: 'app-transaction-management',
  templateUrl: './transaction-management.component.html',
  styleUrls: ['./transaction-management.component.css']
})
export class TransactionManagementComponent implements OnInit {
  searchKey: any = null;
  limit: any;
  pages: any;
  page: any = 1;
  totalvendors: any;
  currUrl: string;
  transaction: any=[];
  constructor(public dataService: DataService, public route: Router) {
    this.route.events.subscribe((event: Event) => {
      if (event instanceof NavigationEnd) {
        this.currUrl = event.url.split('/')[1]
      }
    })
  }

  ngOnInit() {
    this.refresh();
  }

  refresh() {
    this.searchKey = "";
    this.pageChange(1);
    this.searchKey = "";
  }
  pageChange(currentPage) {
    this.dataService.spinnerShow();
    this.dataService.post("admin/transactionHistory", {
      search: this.searchKey,
      limit: 10,
      page: currentPage
    }, 1).subscribe(
      (res: any) => {
        this.dataService.spinnerHide();
        if (res.response_code == 200) {
          this.transaction = res.result.docs;
          this.limit = res.result.limit;
          this.pages = res.result.pages;
          this.page = res.result.page;
          this.totalvendors = res.result.total;
        } else if (res.response_code == 403 || res.response_code == 409 || res.response_code == 404 || res.response_code == 401) {
          this.dataService.errorToastr(res.response_message);
          // this.dataService.logOut();
        } else {
          this.dataService.errorToastr("Cannot get the users.");
        }
      },
      (err: any) => {
        this.dataService.spinnerHide();
        this.dataService.errorToastr("Cannot get the users.")
      }
    );
  }
  searchStatus(value){
    this.searchKey = value;
    this.pageChange(1);
  }
}



