import { Component, OnInit } from '@angular/core';
import { Router, Event, NavigationEnd } from '@angular/router';
import { DataService } from '../../services/data.service';
declare var $;

@Component({
  selector: 'app-sidebar',
  templateUrl: './sidebar.component.html',
  styleUrls: ['./sidebar.component.css']
})
export class SidebarComponent implements OnInit {
  username: any;
  currUrl: string;
  userName: any;
  profilePic: any;
  constructor(public route: Router, public dataService: DataService ) {
    this.route.events.subscribe((event: Event) => {
      if (event instanceof NavigationEnd) {
        this.currUrl = event.url.split('/')[1]
      }
    })
  }

  ngOnInit() {
    this.username = this.dataService.decryptData((JSON.parse(localStorage.getItem("remittance"))).username);

    $('.hamburger').on('click', function () {
      console.log('clicked')
      $(this).toggleClass('is-active');
      $('.navbar-sidebar').slideToggle('500');
    });
    $('.navbar__list li.has-dropdown > a').on('click', function () {
      var dropdown = $(this).siblings('ul.navbar-sidebar__dropdown');
      $(this).toggleClass('active');
      $(dropdown).slideToggle('500');
      return false;
    });
    this.getProfile();
    
  }

  logOut(){
    localStorage.removeItem("remittance");
    this.route.navigate(['/login']);
  }

  getProfile() {
    if (navigator.onLine){
      this.dataService.spinnerShow();
    this.dataService.post('admin/viewAdmin',{adminId: JSON.parse(localStorage.getItem('remittance')).id}, 1).subscribe(
      (response: any) => {
        this.dataService.spinnerHide();
        if (response['response_code'] == 200) {
          this.userName = response.result.userName;
          this.profilePic = response.result.profilePic;
        }
        else if (response.response_code == 403 || response.response_code == 409 || response.response_code == 401 || response.response_code == 404) {
          this.dataService.errorToastr(response.response_message)
          this.dataService.logOut();
        } else {
          this.dataService.errorToastr(response[`response_message`])
        }
      },
      err => {
        this.dataService.spinnerHide();
        this.dataService.errorToastr('Something went wrong')
      })
    } else
    this.dataService.errorToastr('Your internet connection seems to be lost')
  }

}



