import { Observable } from 'rxjs';
import { DataService } from './../../services/data.service';
import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { Router, Event, NavigationEnd } from '@angular/router';
declare var $;

@Component({
  selector: 'app-user-management',
  templateUrl: './user-management.component.html',
  styleUrls: ['./user-management.component.css']
})
export class UserManagementComponent implements OnInit {
  users: any =[];
  searchKey: any = null;
  limit: any;
  pages: any;
  page: any = 1;
  totalUsers: any;
  currUrl: string;
  userId: any;
  constructor(public dataService: DataService, public route: Router) {
    this.route.events.subscribe((event: Event) => {
      if (event instanceof NavigationEnd) {
        this.currUrl = event.url.split('/')[1]
      }
    })
   }

  ngOnInit() {
    this.refresh();
  }

  searchAfterDebounceTime(){
    // Observable.fromEvent(document.getElementById("search"), "keyup").debounceTime(1000).distinctUntilChanged().subscribe(
    //   ()=>{
    //       if(this.searchKey == ""){
    //         this.refresh();
    //       }else{
    //         this.search();
    //       }
    //   }
    // );
  }

  refresh(){
    this.searchKey = "";
    this.search();
    this.searchKey = "";
  }

  searchOnKeydown(event){
    if(event.keyCode == 13){
      // console.log("Keydown event=>>", event);
      if(this.searchKey == ""){
        this.refresh();
      }else{
        this.search();
      }
    }
  }

  pageChange(currentPage){
      this.dataService.spinnerShow();
      this.dataService.post("admin/listUserSearch",{
        search: "",
        limit: 10,
        page: currentPage
      }, 1).subscribe(
        (res: any)=>{
          this.dataService.spinnerHide();
          if(res.response_code == 200){
            this.users = res.result.docs;
            this.limit= res.result.limit;
            this.pages = res.result.pages;
            this.page = res.result.page;
            this.totalUsers = res.result.total;
          }else if(res.response_code == 403 || res.response_code == 409 || res.response_code == 404 || res.response_code == 401){
            this.dataService.errorToastr(res.response_message);
            this.dataService.logOut();
          }else{
            this.dataService.errorToastr("Cannot get the users.");
          }
        },
        (err: any)=>{
          this.dataService.spinnerHide();
          this.dataService.errorToastr("Cannot get the users.")
        }
      );
  }

  search(){
    if(navigator.onLine){
        this.dataService.spinnerShow();
        this.dataService.post("admin/listUserSearch", {
          "search": this.searchKey,
          "limit": 10
        }, 1).subscribe(
          (res:any)=>{
            this.dataService.spinnerHide();
            if(res.response_code == 200){
              this.users = res.result.docs;
              this.limit= res.result.limit;
              this.pages = res.result.pages;
              this.page = res.result.page;
              this.totalUsers = res.result.total;
            }else if(res.response_code == 403 || res.response_code == 409 || res.response_code == 404 || res.response_code == 401){
              this.dataService.errorToastr(res.response_message);
              this.dataService.logOut();
            }else{
              this.dataService.errorToastr("Cannot get the users.");
            }
          },
          (err: any)=>{
            this.dataService.spinnerHide();
            this.dataService.errorToastr("Cannot get the users.");
          }
        );
    }else{
      this.dataService.errorToastr("Your internet connection seems to be lost!");
    }
  }

  userStatus(status, userId){
    this.dataService.spinnerShow();
    let newStatus = status == "ACTIVE"? "BLOCKED" : "ACTIVE";
    // console.log("userid=>>", userId, "\nstatus==>>", status, "\nnew status==>>", newStatus);
    this.dataService.post("admin/actionToUser", {
      "userId": userId,
      "status": newStatus
    }, 1).subscribe(
      (res: any)=>{
        this.dataService.spinnerHide();
        if(res.response_code == 200){
          newStatus == 'ACTIVE' ?
          this.dataService.successToastr("User active now!"):
          this.dataService.errorToastr("User blocked!");
          this.refresh();
        }else if(res.response_code == 403 || res.response_code == 409 || res.response_code == 404 || res.response_code == 401){
          this.dataService.errorToastr(res.response_message);
          this.dataService.logOut();
        }else{
          this.dataService.errorToastr("Cannot get the users.");
        }
      },
      (err: any)=>{
        this.dataService.spinnerHide();
        this.dataService.errorToastr('Something went wrong')
      }
    );
  }

  deleteUserModal(user) {
    this.userId = user._id;
    $('#deleteModal').modal('show');
  }

  deleteUser() {
    this.dataService.spinnerShow();
    this.dataService.post("admin/actionToUser", {
      "userId": this.userId,
      "status": 'DELETED'
    }, 1).subscribe(
      (res: any)=>{
        this.dataService.spinnerHide();
        if(res.response_code == 200){
          this.refresh();
        }else if(res.response_code == 403 || res.response_code == 409 || res.response_code == 404 || res.response_code == 401){
          this.dataService.errorToastr(res.response_message);
          this.dataService.logOut();
        }else{
          this.dataService.errorToastr("Cannot get the users.");
        }
      },
      (err: any)=>{
        this.dataService.spinnerHide();
        this.dataService.errorToastr('Something went wrong')
      }
    );
  }
}



