import { DataService } from './services/data.service';
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { BrowserAnimationsModule } from "@angular/platform-browser/animations";
import { RouterModule,Routes } from '@angular/router';
import { routes } from "./app-routing/app-routing.module";
import { FormsModule } from "@angular/forms"
import { AppComponent } from './app.component';
import { DashboardComponent } from './components/dashboard/dashboard.component';
import { LoginComponent } from './components/login/login.component';
import { ForgotPasswordComponent } from './components/forgot-password/forgot-password.component';
import { ResetPasswordComponent } from './components/reset-password/reset-password.component';
import { SidebarComponent } from './components/sidebar/sidebar.component';
import { UserManagementComponent } from './components/user-management/user-management.component';
import { SettingsComponent } from './components/settings/settings.component';
import { TermsConditionsComponent } from './components/terms-conditions/terms-conditions.component';
import { TermsConditionsEditorComponent } from './components/terms-conditions-editor/terms-conditions-editor.component';
import { CKEditorModule } from '@ckeditor/ckeditor5-angular';
import { NgxSpinnerModule } from "ngx-spinner";
import { ToastrModule } from "ng6-toastr-notifications";
import { ReactiveFormsModule } from '@angular/forms';
import { SpinnerComponent } from './components/spinner/spinner.component';
import { HttpClientModule } from '@angular/common/http';
import { AuthGuard } from './guard/auth.guard';
import {NgxPaginationModule} from 'ngx-pagination';
import { ViewUserDetailsComponent } from './components/view-user-details/view-user-details.component';
import { PageNotFoundComponent } from './components/page-not-found/page-not-found.component';
import { ViewFaqComponent } from './components/view-faq/view-faq.component';
import { AddFaqComponent } from './components/add-faq/add-faq.component';
import { EditFaqComponent } from './components/edit-faq/edit-faq.component';
import { EditProfileComponent } from './components/edit-profile/edit-profile.component';
import { VendorManagementComponent } from './components/vendor-management/vendor-management.component';
import { AddVendorComponent } from './components/add-vendor/add-vendor.component';
import { TransactionManagementComponent } from './transaction-management/transaction-management.component';
import { VendorProfileComponent } from './vendor-profile/vendor-profile.component';

@NgModule({
  declarations: [
    AppComponent,
    DashboardComponent,
    LoginComponent,
    ForgotPasswordComponent,
    ResetPasswordComponent,
    SidebarComponent,
    UserManagementComponent,
    SettingsComponent,
    TermsConditionsComponent,
    TermsConditionsEditorComponent,
    SpinnerComponent,
    ViewUserDetailsComponent,
    PageNotFoundComponent,
    ViewFaqComponent,
    AddFaqComponent,
    EditFaqComponent,
    EditProfileComponent,
    VendorManagementComponent,
    AddVendorComponent,
    TransactionManagementComponent,
    VendorProfileComponent
  ],
  imports: [
    BrowserModule,
    RouterModule.forRoot(routes),
    CKEditorModule,
    BrowserAnimationsModule,
    ToastrModule.forRoot(),
    NgxSpinnerModule,
    ReactiveFormsModule,
    HttpClientModule,
    FormsModule,
    NgxPaginationModule
  ],
  providers: [DataService, AuthGuard],
  bootstrap: [AppComponent]
})
export class AppModule { }



