import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from "@angular/router";
import { DataService } from '../../services/data.service';

@Component({
  selector: 'app-reset-password',
  templateUrl: './reset-password.component.html',
  styleUrls: ['./reset-password.component.css']
})
export class ResetPasswordComponent implements OnInit {

  resetPasswordForm: FormGroup;
  userid: any;

  constructor(private route: Router, public dataService: DataService, public activatedRoute: ActivatedRoute) { }

  ngOnInit() {
    this.resetPasswordForm = new FormGroup({
      password: new FormControl('',Validators.compose([Validators.required, Validators.minLength(8), Validators.maxLength(16)])),
      confirmPassword: new FormControl('', Validators.required)
    });
    this.activatedRoute.params.subscribe((userid) =>
      this.userid = userid.userid
    )
  }

  submit() {
    this.dataService.spinnerShow();
    let data = {
      "userId": this.userid,
      "password":this.resetPasswordForm.value.password
    }
    this.dataService.post("admin/resetPasswordAdmin", data, 0)
    .subscribe(
      (res: any)=>{
        this.dataService.spinnerHide();
        if(res.response_code == 200){
          this.dataService.successToastr(res.response_message);
          this.route.navigate(['/login']);
        }
        else
          this.dataService.errorToastr(res.response_message)
      },
      (err: any)=>{
        this.dataService.spinnerHide();
        this.dataService.errorToastr("Please try again later.")
      })
  }
}



