import { DataService } from './../../services/data.service';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Component, OnInit } from '@angular/core';
import { Router } from "@angular/router";
import { ToastrManager } from "ng6-toastr-notifications";
declare var $;

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  errorMsg: any = "";
  errormessage: any;
  userId: any;
  otp: any;
  adminLoginForm: FormGroup;
  constructor(private route: Router, public dataService: DataService, public toaster: ToastrManager) { }

  ngOnInit() {
    this.adminLoginForm = new FormGroup({
      email: new FormControl(
        '',
         Validators.compose(
           [
            Validators.required,
            // Validators.pattern(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,3}))$/)
          ]
        )
      ),
      password: new FormControl(
        '',
        Validators.compose([Validators.required])
      ),
      rememberMe: new FormControl()
    });
    if(localStorage.getItem("rememberMe")){
      let admin = JSON.parse(localStorage.getItem('rememberMe'));
      this.adminLoginForm.patchValue({
        email: this.dataService.decryptData(admin.email),
        password: this.dataService.decryptData(admin.password)
      })
    }
  }

  onKeydown(event){
    // console.log(event);
    if(event.keyCode == 32){
      return false;
    }
  }

  openModal() {
    if(this.adminLoginForm.valid){
      if(!this.adminLoginForm.value.email.match(this.dataService.emailRegex)){
        this.errorMsg = "Please enter valid email (e.g. johndoe@example.com)";
        // console.log(this.adminLoginForm)
        this.adminLoginForm.get('email').reset();
      }else{
        if(navigator.onLine){
          this.dataService.spinnerShow();
        this.dataService.post("admin/adminLogin", {
          "userType":"ADMIN", "email": this.adminLoginForm.value.email, "password": this.adminLoginForm.value.password
        }, 0)
        .subscribe(
          (res: any)=>{
            this.dataService.spinnerHide()
            if(res.response_code == 200){
              this.userId = res.result.adminId;
              $('#otpModal').modal('show');
            }else{
              this.toaster.dismissAllToastr();
              this.toaster.errorToastr(res.response_message);
            }
          },
          (err: any)=>{
            this.dataService.spinnerHide()
            this.toaster.dismissAllToastr();
            this.toaster.errorToastr("Please try again later.","Unable to sign you in.", {
              toastTimeout: 3000
            })
            // console.log("error",err)
          }
        );
        }else{
          this.toaster.errorToastr("Your internet connection seems to be lost!");
        }
      }
    }else{
      this.toaster.dismissAllToastr();
      this.toaster.errorToastr("Please your valid credentials.");
    }
  }

  signIn() {
    var reg = new RegExp(/^\d+$/);
      if(reg.test(this.otp)) {
      if(!this.adminLoginForm.value.email.match(this.dataService.emailRegex)){
        this.errorMsg = "Please enter valid email (e.g. johndoe@example.com)";
        // console.log(this.adminLoginForm)
        this.adminLoginForm.get('email').reset();
      }else{
        if(navigator.onLine){
          this.dataService.spinnerShow();
        this.dataService.post("admin/adminVerifyOtp", {
          "adminId":this.userId, "otp": this.otp
        }, 0)
        .subscribe(
          (res: any)=>{
            this.dataService.spinnerHide()
            if(res.response_code == 200){
              $('#otpModal').modal('hide');
              if(this.adminLoginForm.value.rememberMe){
                localStorage.setItem("rememberMe", JSON.stringify({
                  email: this.dataService.encryptData(this.adminLoginForm.value.email),
                  password: this.dataService.encryptData(this.adminLoginForm.value.password)
                }));
              }else{
                localStorage.removeItem("rememberMe");
              }
              localStorage.setItem("remittance", JSON.stringify({
                id: res.result._id,
                token: res.result.token,
                username: this.dataService.encryptData(res.result.userName)
              }));
              this.route.navigate(['/dashboard']);
              this.toaster.dismissAllToastr();
              this.toaster.successToastr(res.response_message);
            }else{
              this.toaster.dismissAllToastr();
              this.toaster.errorToastr(res.response_message);
            }
          },
          (err: any)=>{
            this.dataService.spinnerHide()
            this.toaster.dismissAllToastr();
            this.toaster.errorToastr("Please try again later.","Unable to sign you in.")
            // console.log("error",err)
          }
        );
        }else{
          this.toaster.errorToastr("Your internet connection seems to be lost!");
        }
      }
    }else{
      this.toaster.dismissAllToastr();
      this.toaster.errorToastr("Please enter valid otp");
    }
  }
}



