import { ToastrManager } from 'ng6-toastr-notifications';
import { DataService } from './../../services/data.service';
import { ActivatedRoute, Router, Event, NavigationEnd } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { UserManagementComponent } from '../user-management/user-management.component';

@Component({
  selector: 'app-view-user-details',
  templateUrl: './view-user-details.component.html',
  styleUrls: ['./view-user-details.component.css']
})
export class ViewUserDetailsComponent implements OnInit {
  userId: any;
  user: any;
  currUrl: string;

  constructor(public actRoute: ActivatedRoute, public dataService: DataService, public toaster: ToastrManager, public route: Router) {
    this.route.events.subscribe((event: Event) => {
      if (event instanceof NavigationEnd) {
        this.currUrl = event.url.split('/')[1]
      }
    })
   }

  ngOnInit() {
    if(navigator.onLine){
      this.userId= this.actRoute.params;
    // console.log(this.userId._value.id);
    let url = "admin/viewUser/"+this.userId._value.id;
    // console.log(url);
    this.dataService.get(url, 1).subscribe(
      (res: any)=>{
        // console.log("Response", res);
        if(res.response_code === 200){
          this.user = res.result.result;
        }else if(res.response_code == 403 || res.response_code == 409 || res.response_code == 404 || res.response_code == 401){
          this.toaster.dismissAllToastr();
          this.toaster.errorToastr(res.response_message);
          this.dataService.logOut();
        }else{
          this.toaster.dismissAllToastr();
          this.toaster.errorToastr(res.response_message);
        }
      },
      (err: any)=>{
        console.log("Error", err);
      }
    );
    }else{
      this.toaster.dismissAllToastr();
      this.toaster.errorToastr("Your internet connection seems to be lost!");
    }
  }
}



