import { DataService } from './../../services/data.service';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Component, OnInit } from '@angular/core';
import { Router, Event, NavigationEnd } from '@angular/router';
declare var $: any;

@Component({
  selector: 'app-settings',
  templateUrl: './settings.component.html',
  styleUrls: ['./settings.component.css']
})
export class SettingsComponent implements OnInit {
  otp: any;
  changeEmail: FormGroup;
  changePassword: FormGroup;
  setTransactionInterest: FormGroup;
  currUrl: string;

  constructor(public dataService: DataService, public route: Router) { 
    this.route.events.subscribe((event: Event) => {
      if (event instanceof NavigationEnd) {
        this.currUrl = event.url.split('/')[1]
      }
    })
  }

  ngOnInit() {
    this.changeEmail = new FormGroup({
      email: new FormControl('', Validators.compose([
        Validators.required,
        Validators.pattern(this.dataService.emailRegex)
      ]))
    });
    this.changePassword = new FormGroup({
      oldPassword: new FormControl("", Validators.compose([
        Validators.required
      ])),
      newPassword: new FormControl("", Validators.compose([
        Validators.required,
        Validators.minLength(8),
        Validators.maxLength(16)
      ])),
      confirmPassword: new FormControl("", Validators.required)
    });
    this.setTransactionInterest = new FormGroup({
      transactionFee: new FormControl("", Validators.compose([
        Validators.required,
        Validators.pattern(/^(?=.*[1-9])\d+(\.\d{1,3})?$/)
      ])),
      conversionFee: new FormControl("", Validators.compose([
        Validators.required,
        Validators.pattern(/^(?=.*[1-9])\d+(\.\d{1,50})?$/)
      ]))
    })

    this.getCurrentEmail();
    this.getCurrentFees();
  }

  update(param) {
    if(param == 'fees')
      this.getCurrentFees();
    else if(param == 'email')
      this.getCurrentEmail();
  }

  changeEmailAdmin(){
    this.dataService.spinnerShow();
    this.dataService.post("admin/changeEmailAdmin", {
      userId: JSON.parse(localStorage.getItem('remittance')).id,
      email: this.changeEmail.value.email
    },1).subscribe(
      (res: any)=>{
        this.dataService.spinnerHide();
        if(res.response_code == 201){
          $('#otpModal').modal('show');
          // this.dataService.toaster.dismissAllToastr();
          // this.dataService.toaster.successToastr("OTP sent to your mentioned email.");
          // this.changeEmail.reset();
        }else{
          this.dataService.toaster.dismissAllToastr();
          this.dataService.errorToastr(res.response_message);
        }
      },
      (err: any)=>{
        this.dataService.spinnerHide();
        this.dataService.errorToastr('Something went wrong')
      }
    )
  }

  getCurrentEmail() {
    this.dataService.spinnerShow();
    this.dataService.post("admin/viewEmail", {
      _id: JSON.parse(localStorage.getItem('remittance')).id,
    },1).subscribe(
      (res: any)=>{
        this.dataService.spinnerHide();
        if(res.response_code == 200){
          this.changeEmail.setValue({
            email: res.email
          })
          // this.dataService.toaster.dismissAllToastr();
          // this.dataService.toaster.successToastr("OTP sent to your mentioned email.");
          // this.changeEmail.reset();
        }else{
          this.dataService.toaster.dismissAllToastr();
          this.dataService.errorToastr(res.response_message);
        }
      },
      (err: any)=>{
        this.dataService.spinnerHide();
        this.dataService.errorToastr('Something went wrong')
      }
    )
  }

  getCurrentFees() {
    this.dataService.spinnerShow();
    this.dataService.post("admin/currentTransactionFee", {
      _id: JSON.parse(localStorage.getItem('remittance')).id,
    },1).subscribe(
      (res: any)=>{
        this.dataService.spinnerHide();
        if(res.response_code == 200){
          this.setTransactionInterest.setValue({
            transactionFee: res.details.transactionFee,
            conversionFee: res.details.conversionFee
          })
        }else{
          this.dataService.toaster.dismissAllToastr();
          this.dataService.errorToastr(res.response_message);
        }
      },
      (err: any)=>{
        this.dataService.spinnerHide();
        this.dataService.errorToastr('Something went wrong')
      }
    )
  }

  changePasswordFunction() {
    this.dataService.spinnerShow();
    let data = {
      "userId":JSON.parse(localStorage.getItem('remittance')).id,
      "password":this.changePassword.value.oldPassword,
      "newPassword":this.changePassword.value.newPassword
    }
    this.dataService.post("admin/changePasswordAdmin", data, 1)
    .subscribe(
      (res: any)=>{
        this.dataService.spinnerHide();
        // console.log("response",res);
        if(res.response_code == 200){
          this.dataService.successToastr(res.response_message);
          this.changePassword.reset();
        }
        else
          this.dataService.errorToastr(res.response_message)
      },
      (err: any)=>{
        this.dataService.spinnerHide();
        this.dataService.errorToastr("Please try again later.")
      })
  }

  setTransactionIntrst(){
    this.dataService.post('admin/setTransactionInterest',{
      "userId": JSON.parse(localStorage.getItem('remittance')).id,
      "conversionFee": this.setTransactionInterest.value.conversionFee,
      "transactionFee": this.setTransactionInterest.value.transactionFee
    }, 1).subscribe(
      (res: any)=>{
        if(res.response_code == 200){
          this.dataService.toaster.dismissAllToastr();
          this.dataService.successToastr(res.response_message);
        }else{
          this.dataService.toaster.dismissAllToastr();
          this.dataService.errorToastr(res.response_message);
        }
      },
      (err: any)=>{
        console.log("Error",err);
      }
    );
  }

}



