import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Router, Event, NavigationEnd } from '@angular/router';
import { DataService } from '../../services/data.service';

@Component({
  selector: 'app-add-vendor',
  templateUrl: './add-vendor.component.html',
  styleUrls: ['./add-vendor.component.css']
})
export class AddVendorComponent implements OnInit {
  countriesList: any;
  addVendor: FormGroup;
  countryCode: any;
  currUrl: string;
  messageError: string;

  constructor(private dataService: DataService, private route: Router) { 
    this.addVendor = new FormGroup({
      userName: new FormControl('', Validators.compose([Validators.required, Validators.minLength(4)])),
      firstName: new FormControl('', Validators.compose([Validators.required, Validators.pattern(/^[a-zA-Z]*$/)])),
      middleName: new FormControl('', Validators.pattern(/^[a-zA-Z]*$/)),
      lastName: new FormControl('', Validators.compose([Validators.pattern(/^(\w+\s?)*\s*$/)])),
      email: new FormControl('', Validators.compose([Validators.required,Validators.pattern(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,3}))$/)])),
      storeNo: new FormControl('', Validators.compose([Validators.required])),
      street: new FormControl('', Validators.compose([Validators.required])),
      // landMark: new FormControl(''),
      area: new FormControl('', Validators.compose([Validators.required])),
      city: new FormControl('', Validators.compose([Validators.required,Validators.pattern(/^[a-zA-Z]*$/)])),
      state: new FormControl('', Validators.compose([Validators.required,Validators.pattern(/^(\w+\s?)*\s*$/)])),
      pin: new FormControl('', Validators.compose([Validators.required,Validators.pattern(/^[1-9][0-9]{4,5}$/)])),
      country: new FormControl('', Validators.required),
      mobileNumber: new FormControl('', Validators.compose([Validators.required, Validators.pattern(/^[1-9][0-9]{4,14}$/)])),
    });

    this.route.events.subscribe((event: Event) => {
      if (event instanceof NavigationEnd) {
        this.currUrl = event.url.split('/')[1]
      }
    })
  }

  ngOnInit() {
    this.countryListApi();
    this.message();
  }
  message(){
    if(this.addVendor.valid && this.countryCode){
      this.messageError = ''
    }
  }
  //------------------------------ Country JSON ------------------------------//
  countryListApi() {
    this.dataService.getJson().subscribe(response => {
      this.countriesList = response['countries']
    }, error => {
      this.dataService.errorToastr('Could not fetch list of countries. Please try again later')
    })
  }
  //------------------------------ End Country JSON ------------------------------//

  //------------------------------ Change Country ------------------------------//
  selectedCountry() {
    let index = this.countriesList.findIndex(x => x.name == this.addVendor.value.country)
    if(index > -1) {
      console.log(index)
      this.countryCode = this.countriesList[index].dial_code;
    }
  }
  //------------------------------ End Change Country ------------------------------//

  add() {
    if (navigator.onLine){
      if(this.addVendor.valid && this.countryCode){
        this.dataService.spinnerShow();
        this.addVendor.value.countryCode = this.countryCode;
      this.dataService.post('admin/addVendor',this.addVendor.value, 1).subscribe(
        (response: any) => {
          this.dataService.spinnerHide();
          if (response['response_code'] == 200 || response['response_code'] == 201) {
            this.route.navigate(['/vendor-management'])
          }
          else if (response.response_code == 403 || response.response_code == 409 || response.response_code == 401) {
            this.dataService.errorToastr(response.response_message)
            this.dataService.logOut();
          } else {
            this.dataService.errorToastr(response[`response_message`])
          }
        },
        err => {
          this.dataService.spinnerHide();
          this.dataService.errorToastr('Something went wrong')
        })
      }else{
        this.dataService.errorToastr("Some information is missing. Please complete the form");
      }
    } else
    this.dataService.errorToastr('Your internet connection seems to be lost')
  }
}



