import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router, Event, NavigationEnd } from '@angular/router';
import { DataService } from '../../services/data.service';
import { Profile } from 'selenium-webdriver/firefox';

@Component({
  selector: 'app-edit-faq',
  templateUrl: './edit-faq.component.html',
  styleUrls: ['./edit-faq.component.css']
})
export class EditFaqComponent implements OnInit {
  FAQ_id: any;
  question: any;
  answer: any;
  currUrl: string;

  constructor(private activatedRoute: ActivatedRoute, public dataService: DataService, public route: Router) {
    this.route.events.subscribe((event: Event) => {
      if (event instanceof NavigationEnd) {
        this.currUrl = event.url.split('/')[1]
      }
    })
   }

  ngOnInit() {
    this.activatedRoute.params.subscribe((res) =>{
      this.FAQ_id = res.faqId
    })
    this.getFAQ();
  }

  getFAQ() {
    if(navigator.onLine){
      this.dataService.spinnerShow();
      let data = {
        "faq":this.FAQ_id
      }
      this.dataService.post("admin/viewFaqId", data, 1).subscribe(
        (res:any)=>{
          this.dataService.spinnerHide();
          if(res.response_code == 200){
            this.question = res.obj.question;
            this.answer = res.obj.answer;
          }else if(res.response_code == 403 || res.response_code == 409 || res.response_code == 404 || res.response_code == 401){
            this.dataService.errorToastr(res.response_message);
            this.dataService.logOut();
          }else{
            this.dataService.errorToastr(res.response_message);
          }
        },
        (err: any)=>{
          this.dataService.spinnerHide();
          this.dataService.errorToastr("Something went wrong");
        }
      );
  }else{
    this.dataService.errorToastr("Your internet connection seems to be lost!");
  }
  }
  editFAQ() {
    if(navigator.onLine){
      this.dataService.spinnerShow();
      let data = {
        "_id":this.FAQ_id,
        "type":"FAQ",
        "question":this.question,
        "answer":this.answer
      }
      this.dataService.post("admin/updateFaq", data, 1).subscribe(
        (res:any)=>{
          this.dataService.spinnerHide();
          if(res.response_code == 200){
            this.dataService.successToastr(res.response_message);
            window.history.back();
          }else if(res.response_code == 403 || res.response_code == 409 || res.response_code == 404 || res.response_code == 401){
            this.dataService.errorToastr(res.response_message);
            this.dataService.logOut();
          }else{
            this.dataService.errorToastr(res.response_message);
          }
        },
        (err: any)=>{
          this.dataService.spinnerHide();
          this.dataService.errorToastr("Something went wrong");
        }
      );
  }else{
    this.dataService.errorToastr("Your internet connection seems to be lost!");
  }
  }

}



